html {
  box-sizing: inherit; }

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

body {
  background-color: #ffffff;
  min-width: 320px;
  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%;
  height: auto; }

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 5;
  display: block;
  width: 60px;
  height: 60px;
  background: #ffffff;
  cursor: pointer;
  border: none;
  outline: none; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 23px;
  right: 19px;
  width: 25px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222; }

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0; }
  .main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 31px;
    right: 20px;
    width: 24px;
    height: 2px;
    background-color: #222222; }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg); }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg); }

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none; }
  .main-nav--opened .site-list__item {
    display: block; }
  .main-nav--nojs .main-nav__toggle {
    display: none; }
  .main-nav--nojs .site-list__item {
    display: block; }
  .main-nav--nojs .site-list {
    position: static; } }

@media (min-width: 768px) {
  .main-nav__toggle {
    display: none; } }

